.list {
	width: 100%;
	margin-top: 20px;
	margin-bottom: 30px;

	.line{
		height: .7px;
		margin: 50px 0px 20px;
		background-color: aliceblue;
	}
	.listTitle {
		display: block;
		color: white;
		font-size: 24px;
		font-weight: 700;
		margin: 30px 50px;
	}

	.wrapper {
		position: relative;
		.sliderArrow {
			width: 50px;
			height: 100%;
			background-color: rgb(22, 22, 22, 0.5);
			color: white;
			position: absolute;
			z-index: 99;
			top: 0;
			bottom: 0;
			margin: auto;
			cursor: pointer;

			&.left {
				left: 0;
			}

			&.right {
				right: 0;
			}
		}
		.container {
			margin-left: 50px;
			display: flex;
			margin-top: 10px;
			width: max-content;
			transform: translateX(0px);
			transition: all 1s ease;
		}
	}
} 
