.listItem {
	width: 225px;
	height: 250px;
	background-color: var(--main-color);
	margin-right: 5px;
	overflow: hidden;
	color: white;
	cursor: pointer;

	img {
		width: 100%;
		height: 100%;
		object-fit: fill;
	}

	video {
		width: 100%;
		height: 250px;
		object-fit: cover;
		position: absolute;
		top: 0;
		left: 0;
	}

	&:hover {
		width: 325px;
		height: 400px;
		position: absolute;
		top: -150px;
		-webkit-box-shadow: 0px 0px 15px 0px rgba(255, 255, 255, 0.07);
		box-shadow: 0px 0px 15px 0px rgba(255, 255, 255, 0.07);
		border-radius: 5px;

		img {
			height: 250px;
		}

		.itemInfo {
			display: flex;
			flex-direction: column;
			padding: 5px;

			.icons {
				display: flex;
				margin-bottom: 10px;
				a{
					color: #dfdfdf;
				}
				.icon {
					border: 2px solid white;
					padding: 5px;
					border-radius: 50%;
					margin-right: 10px;
					font-size: 25px;
				}
			}
			.itemInfoTop {
				display: flex;
				align-items: center;
				margin-bottom: 10px;
				font-size: 14px;
				font-weight: 600;
				color: gray;

				.limit {
					border: 1px solid gray;
					padding: 1px 3px;
					margin: 0 10px;
				}
			}

			.desc {
				font-size: 13px;
				margin-bottom: 10px;
			}

			.genre {
				font-size: 14px;
				color: lightgray;
			}
		}
	}
}
