@import '../../App.scss';

.navbar {
	width: 100%;
	color: white;
	font-size: 14px;
	position: fixed;
	top: 0;
	z-index: 999;
	background: linear-gradient(
		to top,
		transparent 0%,
		rgb(0, 0, 0, 0.3) 50%
	);
	@include mobile {
		width: 100%;
	}

	&.scrolled {
		background-color: var(--main-color);
	}

	.container {
		padding: 0px 50px;
		display: flex;
		align-items: center;
		justify-content: space-between;
		height: 70px;

		.left {
			display: flex;
			align-items: center;
			a{
				color: aliceblue;
				text-decoration: none;
			}
			img {
				height: 25px;
				margin-right: 40px;
				@include mobile {
					// display: none;
				}
			}

			span {
				margin-right: 20px;
				cursor: pointer;
			}
		}

		.right {
			display: flex;
			align-items: center;
			a{
				color: #fff;
				text-decoration: none;
			}
			.icon {
				margin: 0px 15px;
				cursor: pointer;
			}

			img {
				width: 30px;
				height: 30px;
				border-radius: 5px;
				object-fit: cover;
				cursor: pointer;
			}

			.profile {
				.options {
					display: none;
					background-color: var(--main-color);
					border-radius: 5px;
				}

				span {
					padding: 10px;
					cursor: pointer;
				}

				&:hover {
					.options {
						display: flex;
						flex-direction: column;
						position: absolute;
					}
				}
			}
		}
	}
}
